.header {
  grid-area: header;
  background-color: #4c77a6;
  
}


.header img {
  max-width: 40px;

}

.header_login_field {

  line-height: 40px;
  margin-right: 20px;
  float: right;
  width: 200px;
  display:table-cell;
  

  
}

.header_login_field img {

  border-radius: 18px;  
  width: 36px;
  height: 36px;
  float: left;
 
  margin: 2px 0px;
  
  
}

.header_login_name {
color: white;
font-size: 12.5px;
font-weight: 700;
float: left;
padding-left: 10px;
padding-right: 20px;


}

.header_login_name:hover{
  background-color: #2a5885;
}

.header_login_button {

  float: right;
  vertical-align: middle;
  
}



