.main {
  grid-area: profile;
  box-shadow: 0px 0px 3px 0px grey;
  background-color: white;
  border-radius: 5px;
  padding:10px;
display: grid;
grid-template-columns: 1fr 2fr;
grid-gap: 10px;

}

.main img {

  width: calc(100% - 5px);



}





.info {
  padding: 0 0 15px;
  border-bottom: 1px solid #e7e8ec;


}

.name {

  font-size: 19px;
  line-height: 25px;
  font-weight: 400;
}

.status {
  line-height: 16px;
  font-size: 13px;
  font-weight: 400;
  color: #000;
  cursor: pointer;
  margin: -4px -6px -4px;
padding: 4px 6px 4px;

}


.status:hover{

  background-color: #f5f7fa;
}




.profile_info {

  display: grid;
  grid-template-columns: 3fr 1fr;
}

.contacts {
display: grid;
grid-template-columns: 2fr 3fr;
grid-template-rows: 40px 1fr;
grid-template-areas: 
"header header"
"left right" ;

}
.contacts_header {
  grid-area: header;
  line-height: 40px;
  vertical-align: text-top;

  
}

.contact_item {
  grid-area: left;
  color: #828282;;
}

.contact_link {
  grid-area: right;

}
.job_info{
  text-align: center;
  border-left: 1px solid #e7e8ec;

}
.job_info img {
  max-width: 100px;

}

.job_header{

  padding: 10px 0;
}
 .change_status {
color: #939393;

 }

